/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.header {
  position: relative;
  height: 100vh;
  background-image: url("../images/header-background-1.jpg");
  background-size: cover;
  background-position: center; }

.header .title {
  height: 60px;
  text-transform: uppercase;
  opacity: 0.95;
  background-color: var(--blue-green);
  color: white; }

.main_menu {
  height: 45px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
  background-color: var(--aqua);
  opacity: 0.9; }

.main_menu .menu_link {
  text-decoration: none;
  text-transform: uppercase;
  color: white; }

:root {
  --blue-green: #099d8d;
  --aqua: #14d9c4; }

.hero {
  position: absolute;
  bottom: 0;
  left: 15px;
  right: 15px;
  height: 2em;
  transform: translateY(-50%);
  background-color: white; }
